.aim {
  position: relative;
  padding-top: 22px;
  padding-bottom: 45px;

  &::before {
    content: "";
    position: absolute;
    bottom: 26px;
    height: 1px;
    width: 100vw;
    background-color: #d9d9d9;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: 26px;
    height: 1px;
    width: 100vw;
    left: -100vw;
    background-color: #d9d9d9;
  }
}

@media (min-width: @tablet-width) {
  .aim {
    padding: 23px 27px 27px;
    outline: 1px solid #e7e7e7;

    &::before {
      display: none;
    }

    &::after {
      display: none;
    }
  }
}

@media (min-width: @desktop-width) {
  .aim {
    padding: 25px 28px;
  }
}
